<template>
    <div id="app">
        <pulse-loader :color="color" :size="size" :margin="margin" :radius="radius"></pulse-loader>
        <div id="menu" class="ui secondary menu" v-bind:class="{menu_show: scrollMenu }">
            <ul id="mainmn">
                <li><router-link to="/" :class="{menu_show_font : scrolled > 200}">MAIN</router-link></li>
            </ul>
            <ul id="submn">
              <li><router-link to="/notice" :class="{menu_show_font : scrolled > 200}">공지사항</router-link></li>
              <li><a v-on:click="problemLoginCheck" :class="{menu_show_font : scrolled > 200}">리스트</a></li>
              <li><a v-on:click="rankLoginCheck" :class="{menu_show_font : scrolled > 200}">랭킹</a></li>
              <li v-if="loginState">
                <router-link v-if="userRating == 3" to="/admin" :class="{menu_show_font : scrolled > 200}">관리자페이지 - {{username}}님</router-link>
                <router-link v-else to="/mypage" :class="{menu_show_font : scrolled > 200}">{{username}}님</router-link>
                <a @click="logout" :class="{menu_show_font : scrolled > 200}">로그아웃</a>
              </li>
                <li v-if="loginState == false"><a @click="openModal" :class="{menu_show_font : scrolled > 200}">SIGN</a></li>
            </ul>
        </div>
        <div v-if="loginState == false" id="sign">
            <div class="ui modal">
              <div class="conclo">
                <i class="close icon" v-on:click="closeModal"></i>
              </div>
                <div class="login_form" v-if="signState">
                    <div class="description">
                        <div class="ui two column centered grid">
                            <div class="container">
                                <div class="culnmn">
                                  <div class="signhead">
                                    <h1 class="ui grey header">로그인</h1>
                                  </div>
                                    <form class="ui large form">
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="user icon"></i>
                                                <input type="text" name="userid" placeholder="아이디" v-model="userid" v-on:keydown.enter="submit">
                                            </div>
                                        </div>
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="lock icon"></i>
                                                <input type="password" name="password" placeholder="비밀번호" v-model="password" v-on:keydown.enter="submit">
                                            </div>
                                        </div>
                                        <button type="button" v-on:click="submit" class="ui fluid large teal submit button submitButton">
                                            로그인
                                        </button>
                                    </form>
                                    <button v-on:click="signState = false" class="ui button black signButton">
                                        회원가입하기
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="register_form" v-else>
                    <div class="description">
                        <div class="ui two column centered grid">
                            <div class="container">
                                <div class="culnmn">
                                  <div class="signhead">
                                    <h1 class="ui grey header">회원가입</h1>
                                  </div>
                                    <form class="ui large form">
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="user icon"></i>
                                                <input type="text" name="userid" placeholder="아이디" v-model="userid" v-on:keydown.enter="submit">
                                            </div>
                                        </div>
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="lock icon"></i>
                                                <input type="password" name="password" placeholder="비밀번호" v-model="password"v-on:keydown.enter="submit">
                                            </div>
                                        </div>
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="user icon"></i>
                                                <input type="text" name="username" placeholder="이름" v-model="username"v-on:keydown.enter="submit">
                                            </div>
                                        </div>
                                        <div class="field">
                                            <div class="ui left icon input">
                                                <i class="student icon"></i>
                                                <input type="text" name="studentcode" placeholder="학번" v-model="studentcode" v-on:keydown.enter="submit" v-on:keypress="isNumber(event)">
                                            </div>
                                        </div>
                                        <div class="field">
                                            <div class="ul left icon input">
                                                <div class="lock icon">
                                                    <vue-recaptcha sitekey="6LejvBgUAAAAAE_F7SjXLPzPiyroAqAdXvBhk7IG"></vue-recaptcha>
                                                    <!--<vueRecaptcha-->
                                                            <!--ref="recaptcha"-->
                                                            <!--@verify="onVerify"-->
                                                            <!--@expired="onExpired"-->
                                                            <!--:sitekey="opts.sitekey"-->
                                                            <!--:options="opts">-->
                                                    <!--</vueRecaptcha>-->
                                                    <button @click="resetRecaptcha"> Reset ReCAPTCHA </button>
                                                </div>
                                            </div>
                                        </div>
                                        <div id="html_element" ></div>
                                        <div v-on:click="submit" v-on:keyup.enter="submit" class="ui fluid large teal submit button submitButton">
                                            회원가입
                                        </div>
                                    </form>
                                    <button v-on:click="signState = true" class="ui button black signButton">
                                        로그인하기
                                    </button>
                                </div>
                            </div>
                        </div>

                    </div>

                </div>

            </div>
        </div>
        <vue-progress-bar></vue-progress-bar>
        <transition name="sigoPage" mode="out-in">
      <router-view></router-view>
      </transition>
        </div>
    </div>
</template>

<script>
  import VueRecaptcha from 'vue-recaptcha';
  import PulseLoader from 'vue-spinner/src/PulseLoader.vue';

  export default {
    components: {
      VueRecaptcha,
      PulseLoader,
    },
    props: {
      loading: {
        type: Boolean,
        default: true,
      },
      color: {
        type: String,
        default: '#5dc596',
      },
      size: {
        type: String,
        default: '15px',
      },
      margin: {
        type: String,
        default: '0 auto',
      },
      radius: {
        type: String,
        default: '100%',
      },
    },
    data() {
      return {
        loginState: false,
        signState: true,
        userid: '',
        password: '',
        username: '',
        studentcode: '',
        loginResult: '',
        userToken: '',
        userRating: '',
        linkInfo: '',
        scrolled: false,
        scrollMenu: false,
        solveMenu: false,
        on: false,
        infoSubmit: false,
        userCount: 0,
        opts: {
          sitekey: '6LejvBgUAAAAAE_F7SjXLPzPiyroAqAdXvBhk7IG',
        },
      };
    },
    ready() {
      alert('ready');
    },
    created() {
	    this.$Progress.start();
	    //  hook the progress bar to start before we move router-view
	    this.$router.beforeEach((to, from, next) => {
		    //  does the page we want to go to have a meta.progress object
		    if (to.meta.progress !== undefined) {
			    const meta = to.meta.progress;
			    // parse meta tags
			    this.$Progress.parseMeta(meta);
		    }
		    //  start the progress bar
		    this.$Progress.start();
			    //  continue to next page
            next();
        });
      const ROOT_URL = 'https://algorithm.ayanami.kr/api';
      this.$http.defaults.baseURL = ROOT_URL;
//      토큰 테스트
      this.userToken = this.$cookie.get('userToken');
      if (this.userToken != null) {
        this.userToken = this.$cookie.get('userToken');
        this.$http.defaults.headers.common.Authorization = this.userToken;
        this.$http.get('users/my-info')
          .then((resInfo) => {
              this.userRating = resInfo.data.user.rating;
              this.username = resInfo.data.user.username;
              this.loginState = true;
          })
          .catch((error) => {
	          this.$swal({
			          title: '유저 조회 실패',
			          text: error,
			          type: 'error',
		          });
          });
      }
      this.$router.afterEach((to, from) => {
		    //  finish the progress bar
	      this.$Progress.finish();
      });
      this.$Progress.finish();
      window.addEventListener('scroll', this.scrollFunction);
    },
    destroyed() {
      window.removeEventListener('scroll', this.scrollFunction);
    },
    methods: {
      scrollFunction() {
        this.scrolled = window.scrollY;
        if (this.scrolled > 200) {
          this.scrollMenu = true;
        } else {
          this.scrollMenu = false;
        }
      },
      problemLoginCheck() {
        if (this.$cookie.get('userToken') == null) {
          this.$swal(
            '입장 불가',
            '로그인을 하셨나요?',
            'error',
          );
        } else {
          this.$router.push({
            name: 'problems',
          });
        }
      },
      rankLoginCheck() {
        if (this.$cookie.get('userToken') == null) {
          this.$swal(
            '입장 불가',
            '로그인을 하셨나요?',
            'error',
          );
        } else {
          this.$router.push({
            name: 'rank',
          });
        }
      },
      cookieDel() {
        // 쿠키 삭제
        this.$cookie.delete('userToken');
        this.$cookie.delete('userRating');
        this.$cookie.delete('userName');
      },
      isNumber(number) {
        const evt = (number) || window.event;
        const charCode = (evt.which) ? evt.which : evt.keyCode;
        if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
          evt.preventDefault();
          return false;
        }
        return true;
      },
      logout() {
        this.$swal({
          title: '로그아웃합니다',
          text: '이 상자는 2초후에 사라집니다',
          timer: 2000,
        },
        );
        this.cookieDel();
        this.$router.push({
          name: 'index',
        });
        this.loginState = false;
      },
      // 폼 모달
      openModal() {
        $('.ui.modal').modal('show');
      },
      closeModal() {
          $('.ui.modal').modal('hide');
      },
      onloadCallback() {
        alert('grecaptcha is ready!');
      },
      onVerify(response) {
        console.log(`Verify:  + ${response}`);
      },
      onExpired() {
        console.log('Expired');
      },
      resetRecaptcha() {
        this.$refs.recaptcha.reset(); // Direct call reset method
      },
      submit() {
        let errMsg;
        if (this.signState === true) {
              this.$http.post('users/signin', {
                userid: this.userid,
                password: this.password,
              })
              .then((resSign) => {
                this.userToken = resSign.data.token;
                this.$http.defaults.headers.common.Authorization = this.userToken;
                // 헤더 토큰 등록
                this.$http.get('users/my-info')
                  // 로그인 성공
                  .then((resInfo) => {
                    if (resInfo.status === 200) {
                      this.loginState = true;
                      this.username = resInfo.data.user.username;
                      this.closeModal();
                      this.loginState = true;
                      this.userRating = resInfo.data.user.rating;
                      // Cookie : 이름 , 내용 , 만료기간 , 도메인
                      this.$cookie.set('userToken', this.userToken, 1);
                      // 쿠키 값 출력
                      this.$swal(
                        '로그인 성공',
                        '안녕하세요!',
                        'success',
                      );
                    }
                  })
                  // 토큰인증 실패
                  .catch((err) => {
                      this.closeModal();
                      this.$swal({
                        title: '로그인 실패',
                        text: err,
                        type: 'error',
                    });
                  });
              })
              .catch((err) => {
                if (err.response.data.message === 'account false') {
                    errMsg = '관리자의 승인을 기다려주세요';
                } else if (err.response.data.message === 'login fail') {
                    errMsg = '아이디 또는 비밀번호가 잘못되었습니다';
                } else if (err.response.data.message === 'validation error') {
                    errMsg = '정보를 모두 입력해주세요';
                } else if (err.response.data.message === 'fail rating excess') {
                    errMsg = '5회 이상 틀려 확인정보를 입력해 주세요';
                    this.infoSubmit = true;
                }
                  this.$swal({
                      title: '로그인 실패',
                      text: errMsg,
                      type: 'error',
                  })
                    .then(() => {
                      if (this.infoSubmit === true) {
                        this.failReset();
                        this.infoSubmit = '';
                      }
                    });
              });
            } else {
              // 회원가입
                this.$http.post('users/signup', {
                username: this.username,
                userid: this.userid,
                password: this.password,
                studentcode: this.studentcode,
              })
              .then((resRegister) => {
                  this.closeModal();
                  const username = resRegister.data.username;
                this.$swal({
                  title: '회원가입 성공',
                  text: `안녕하세요 ${username}님`,
                  type: 'success',
                });
              })
              .catch((error) => {
                this.closeModal();
                if (error.response.data.message === 'validation error') {
                  errMsg = '정보를 제대로 기입해주세요';
                  }
                  this.$swal({
                    title: '회원가입 실패',
                    text: errMsg,
                    type: 'error',
                  });
              });
            }
      },
      failReset() {
        this.$swal.setDefaults({
          input: 'text',
          confirmButtonText: '다음',
          cancelButtonText: '취소',
          showCancelButton: true,
          animation: true,
          progressSteps: ['1', '2', '3'],
        });
        const steps = [
          '아이디를 입력해주세요',
          '이름을 입력해주세요',
          '학번을 입력해주세요',
        ];
        this.$swal.queue(steps).then((result) => {
          this.$swal.resetDefaults();
          this.$http.post('users/failReset', {
              userid: result[0],
              username: result[1],
              studentcode: result[2],
            })
            .then(() => {
              this.$swal({
                title: '확인 완료되었습니다',
                text: '다시 로그인 해주세요',
                type: 'success',
              });
            })
            .catch(() => {
              this.$swal({
                title: '확인 실패하였습니다',
                text: '다시 시도해주세요',
                type: 'error',
              })
              .then(() => {
                this.failReset();
              });
            });
        });
      },
  },
  };
</script>
<style src="./assets/css/app.css" scoped>
/*body{
  background-color: rgb(40,40,40);
}*/
</style>


